.prime-document {
  padding: 32px;
  padding-bottom: 128px;
  max-width: 1024px;
  margin-left: auto;
  margin-right: auto;
}
.prime-document .ant-tabs.ant-tabs-card {
  overflow: visible;
}
.prime-document .ant-tabs.ant-tabs-card > .ant-tabs-content {
  margin-top: -18px;
}
.prime-document .ant-tabs.ant-tabs-card > .ant-tabs-content > .ant-tabs-tabpane {
  background: #fff;
  padding: 32px;
  border-radius: 3px;
}
.prime-document .ant-tabs.ant-tabs-card > .ant-tabs-bar {
  border-color: transparent;
}
.prime-document .ant-tabs.ant-tabs-card > .ant-tabs-bar .ant-tabs-tab {
  border-color: transparent;
  background: transparent;
}
.prime-document .ant-tabs.ant-tabs-card > .ant-tabs-bar .ant-tabs-tab-active {
  border-color: #fff;
  background: #fff;
}
.prime-document .ant-tabs-tabpane > .ant-form-item:last-child {
  margin-bottom: 0;
}
.prime-document .ant-form-item {
  border-left: 3px solid #f8f8f8;
  padding-left: 16px;
  transition: border-left-color ease-in-out 0.33s;
}
.prime-document .ant-form-item:focus-within {
  border-left-color: #e5e5e5;
}
.prime-document .ant-form-item,
.prime-document .ant-form-item-with-help {
  margin-bottom: 32px;
}
.prime-document .ant-form-item-label {
  line-height: normal;
  margin-bottom: 4px;
  margin-top: -4px;
}
.prime-document .ant-form-item-label label {
  opacity: 0.35;
  font-family: "Noto Sans SC", "Source Sans Pro", -apple-system, BlinkMacSystemFont, "Segoe UI", "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "Helvetica Neue", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-weight: 400;
  font-size: 13px;
}
.prime-document .ant-form-item-label label:after {
  display: none;
}
.prime-document .ant-form-item-control {
  line-height: normal;
}
.prime-document .ant-form-item-control .ant-form-explain {
  margin-top: 8px;
  font-size: 12px;
}
.prime-document .ant-input {
  border-width: 0;
  outline: none;
  border-radius: 0px;
  margin-left: 0;
  margin-bottom: 0;
  padding: 0;
}
.prime-document .ant-input:focus,
.prime-document .ant-input:hover {
  border: none !important;
  box-shadow: none !important;
}
.prime-document .ant-input,
.prime-document .ant-cascader-picker-label,
.prime-document .ant-time-picker-input {
  font-size: 16px;
  font-family: "Noto Sans SC", "Source Sans Pro", -apple-system, BlinkMacSystemFont, "Segoe UI", "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "Helvetica Neue", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-weight: 300;
}
.prime-document .ant-input-lg {
  height: auto;
}
.prime-document .bf-content {
  height: auto !important;
  padding-bottom: 0;
}
.prime-document .bf-container .public-DraftEditor-content > div {
  padding-bottom: 0;
}
.prime-document .bf-controlbar {
  padding: 0;
  margin-bottom: 8px;
  box-shadow: inset 0 -1px 0 0 rgba(0, 0, 0, 0.075);
  position: sticky;
  top: 0;
  background: white;
  z-index: 7;
}
.prime-document .bf-container .public-DraftEditor-content {
  padding: 0;
}
.prime-document .ant-calendar-picker-input,
.prime-document .ant-time-picker-input {
  border: none;
  box-shadow: 0 1px 7px rgba(0, 0, 0, 0.075) !important;
  padding: 8px;
  border-radius: 5px;
}
.prime-document .ant-calendar-picker-input:focus,
.prime-document .ant-time-picker-input:focus,
.prime-document .ant-calendar-picker-input:hover,
.prime-document .ant-time-picker-input:hover {
  box-shadow: 0 1px 7px rgba(0, 0, 0, 0.075) !important;
}
.prime-document .ant-select {
  margin-top: 8px;
}
.prime-document .ant-select-selection {
  border: none;
  box-shadow: 0 1px 7px rgba(0, 0, 0, 0.075) !important;
  border-radius: 5px;
  min-height: 40px;
  padding: 4px 8px;
}
.prime-document .ant-select-selection:focus,
.prime-document .ant-select-selection:hover {
  box-shadow: 0 1px 7px rgba(0, 0, 0, 0.075) !important;
}
.prime-document .ant-calendar-picker,
.prime-document .ant-time-picker {
  margin-top: 8px;
  min-width: 150px;
}
.prime-document .ant-cascader-input {
  padding: 8px;
  border: none !important;
  box-shadow: none !important;
}
.prime-document .ant-cascader-picker-label {
  padding-left: 0px;
}
.prime-document .prime-slice {
  margin: -32px;
  margin-bottom: 16px;
  background-color: #f0f2f5;
}
.prime-document .prime-slice > .ant-form-item-label {
  margin-bottom: 8px;
}
.prime-document .prime-slice-spacer-top {
  height: 3px;
  width: 100%;
  background-color: white;
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
  padding-top: 32px;
  margin-bottom: 32px;
}
.prime-document .prime-slice-spacer-bottom {
  height: 3px;
  width: 100%;
  background-color: white;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  margin-top: 32px;
}
.prime-document .prime-slice:nth-last-child(2) {
  margin-bottom: -32px;
}
.prime-document .prime-slice:nth-last-child(2) .prime-slice-spacer-bottom {
  display: none;
}
.prime-document .prime-slice-item {
  margin-bottom: 16px;
  border: none;
  border-radius: 3px;
}
.prime-document .prime-slice-item .ant-card-body {
  padding: 32px;
  padding-bottom: 0;
}
.prime-document .prime-slice-item-actions {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 10;
  flex-direction: row;
}
.prime-document .prime-slice-add {
  border: none !important;
  box-shadow: none;
}
.prime-document .prime-slice-add:hover {
  box-shadow: 0 2px 1px rgba(0, 0, 0, 0.025);
}
.prime-document .prime-slice-item-button {
  padding: 12px 12px;
  cursor: pointer;
  transition: background-color ease-in-out 0.33s;
}
.prime-document .prime-slice-item-button.disabled {
  color: #eee;
  cursor: default;
}
.prime-document .prime-slice-item-button.disabled:hover {
  background-color: transparent;
}
.prime-document .prime-slice-item-button:hover {
  background-color: #f8f8f8;
}
.prime-document .prime-slice-item-button:last-child {
  border-top-right-radius: 3px;
}
.prime-document .prime-group {
  border-left: 0;
  margin-left: -32px;
  margin-right: -32px;
  background-color: #f8f8f8;
  padding-top: 32px;
  padding-right: 32px;
  padding-bottom: 32px;
}
.prime-document .prime-group > .ant-form-item-label {
  margin-bottom: 8px;
}
.prime-document .prime-group:nth-last-child(2) {
  margin-bottom: -64px;
}
.prime-document .prime-group-item {
  position: relative;
  border: none;
  padding: 0;
  margin: 0;
  margin-bottom: 16px;
  border-radius: 3px;
}
.prime-document .prime-group-item > .ant-card-body {
  padding: 16px;
  padding-bottom: 0;
  padding-top: 32px;
}
.prime-document .prime-slice .prime-group {
  margin-left: -32px;
  margin-right: -32px;
  padding-right: 32px;
  padding-bottom: 32px;
  margin-bottom: 0;
  padding-left: 32px;
}
.prime-document .ant-calendar-picker-clear,
.prime-document .ant-calendar-picker-icon {
  width: 16px;
  height: 16px;
}
