.prime__settings__content {
  padding: 32px;
}
.prime__settings__card.ant-card .ant-card-body {
  display: flex;
  flex-direction: row;
  padding-left: 0;
  padding-right: 0;
}
.prime__settings__left {
  min-width: 220px;
}
.prime__settings__right {
  flex: 1;
  padding-left: 32px;
  padding-right: 32px;
  min-height: calc(100vh - 200px);
}
.prime__settings__menu {
  height: 100%;
}
.prime__settings__menu.ant-menu:not(.ant-menu-horizontal) .ant-menu-item-selected {
  background-color: rgba(209, 222, 222, 0.35);
}
